import { useState, useEffect } from 'react';
import { getBlogs } from '../api/api';

const SearchFilter = ({ onSearch, onFilter }) => {
  const [search, setSearch] = useState("");
  const [categories, setCategories] = useState([]);

  // Internal styles
  const styles = {
    container: {
      padding: '16px',
      display: 'flex',
      gap: '16px',
      alignItems: 'center',
      backgroundColor: '#f9fafb',
      borderBottom: '1px solid #e5e7eb'
    },
    searchInput: {
      padding: '8px',
      border: '1px solid #d1d5db',
      borderRadius: '4px',
      width: '100%',
      maxWidth: '384px',
      fontSize: '14px',
      outline: 'none',
      transition: 'border-color 0.2s ease'
    },
    searchInputFocus: {
      borderColor: '#3b82f6',
      boxShadow: '0 0 0 3px rgba(59, 130, 246, 0.1)'
    },
    select: {
      padding: '8px',
      border: '1px solid #d1d5db',
      borderRadius: '4px',
      fontSize: '14px',
      backgroundColor: 'white',
      cursor: 'pointer',
      outline: 'none',
      minWidth: '160px',
      transition: 'border-color 0.2s ease'
    },
    selectFocus: {
      borderColor: '#3b82f6',
      boxShadow: '0 0 0 3px rgba(59, 130, 246, 0.1)'
    }
  };

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const { data } = await getBlogs();
        const allCategories = [...new Set(data.flatMap((blog) => blog.categories))];
        setCategories(allCategories);
      } catch (err) {
        console.error('Error fetching categories:', err);
      }
    };
    fetchCategories();
  }, []);

  const handleSearch = (e) => {
    setSearch(e.target.value);
    onSearch(e.target.value);
  };

  const handleFilter = (e) => {
    onFilter(e.target.value);
  };

  return (
    <div style={styles.container}>
      <input
        type="text"
        placeholder="Search blogs..."
        value={search}
        onChange={handleSearch}
        style={styles.searchInput}
        onFocus={(e) => {
          Object.assign(e.target.style, styles.searchInputFocus);
        }}
        onBlur={(e) => {
          e.target.style.borderColor = styles.searchInput.borderColor;
          e.target.style.boxShadow = 'none';
        }}
      />
      <select 
        onChange={handleFilter} 
        style={styles.select}
        onFocus={(e) => {
          Object.assign(e.target.style, styles.selectFocus);
        }}
        onBlur={(e) => {
          e.target.style.borderColor = styles.select.borderColor;
          e.target.style.boxShadow = 'none';
        }}
      >
        <option value="">Filter by Category</option>
        {categories.map((category) => (
          <option key={category} value={category}>{category}</option>
        ))}
      </select>
    </div>
  );
};

export default SearchFilter;